import React from 'react';
import { homeObjOne,homeObjTwo, homeObjThree } from './Data';
import { InfoSection } from '../../components';
import { Button } from '../../globalStyles';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

function Home() {
  return (
    <>
      <InfoSection {...homeObjOne} />
        
      <InfoSection {...homeObjTwo} />
      <B><center>
      <A><a href= 'https://drive.google.com/file/d/1iTAkT8qHT3qql97Fi0lAHsjjzC0s2oT4/view?usp=sharing'><h1>Class 9</h1></a></A>  
      <A><a href= 'https://drive.google.com/file/d/1330pY9uyUlI6foQw3HYyWE-Lno9F_5aP/view?usp=sharing'><h1>Class 10</h1></a></A>    
      <A><h1>Class 11</h1></A>
      <A><h1>Class 12</h1></A>  </center></B>
      <InfoSection {...homeObjThree} />
    </>
  );
}

export default Home;

const B = styled.div`
  
`;

const A = styled.button`

  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 10%;
  text-align: center;
  width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    -o-moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
  background-image: linear-gradient(to right, #667eea, #764ba2, #6B8DD6, #8E37D7);
  box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
  cursor: pointer;

  &:hover {
    background-position: 100% 0;
    -o-moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
  }
  &:focus {
    outline: none;
  }

  @media screen and (max-width: 960px) {
    width: 80%;
  }

  `;