import React from 'react';
import styled from 'styled-components';
import { homeObjOne } from './Data';
import { Custom } from '../../components';

const A = styled.div`
  color: #610cef;
  margin-top: 5px;
`;


export const QC = styled.div`
  color: #888b96;
  font-size: medium;
  padding-left: 20px;
  padding-top: 50px;
  font-weight: bold;
`;

const AC = styled.div`
  color: #888b96;
  font-size: small;
  padding-left: 20px;
  padding-top: 20px;
  font-weight: bold;
`;


function Booklet() {
  return (
    
    <>
    <A><h2><center>Results</center></h2></A>
    <AC><h3><center>Note- Results of only top 25 are declared.
      </center></h3></AC>
      <QC><center>Ranking Stats-</center></QC>
      <AC><center>1st to 5th - 50% Scholarship + Prizes + Goodies </center></AC>
      <AC><center>6th to 10th - 40% Scholarship + Goodies</center></AC>
      <AC><center>11th to 15th - 30% Scholarship + Goodies</center></AC>
      <AC><center>16th to 20th - 20% Scholarship + Goodies</center></AC>
      <AC><center>21st to 25th - 10% Scholarship + Goodies</center></AC>

      <Custom {...homeObjOne} />
    </>
  );
}

export default Booklet;

